<template>
  <v-container>

    <v-row class="text-center">
      <v-col cols="12" class="d-flex message-block">
        <v-card
            style="position: absolute; top: 50px; bottom: 50px; left: 50%; transform: translateX(-50%)"
            elevation="5"
            max-width="900"
            width="100%"
            :height="comments.length <=3 ? '450px' : ''"
            class="ma-auto pa-5"
        >

          <v-card
              class="d-flex flex-column"
              elevation="0"
          >
            <v-list-item
                class="pr-0"
                dense
            >
              <v-list-item-avatar
                  class="mb-0"
                  left
                  color="#C4C4C4"
                  size="36"
              >
                AA
              </v-list-item-avatar>
              <v-textarea
                  class="mt-2"
                  rows="1"
                  placeholder="Добавить комментарий..."
                  outlined
                  hide-details
                  no-resize
                  dense
                  auto-grow
                  wrap="hard"
                  maxlength="3000"
                  v-model.trim="commentObj.comment_text"
              >

              </v-textarea>
            </v-list-item>

            <v-row class="ma-0 mt-3 align-center justify-space-between">

              <div class="ml-15">
                <v-btn
                    class="mr-2"
                    icon
                    color="#B3B3B3"
                >
                  <v-icon dark size="28">mdi-paperclip</v-icon>
                </v-btn>

                <v-btn
                    icon
                    color="#B3B3B3"
                >
                  <v-icon size="28" dark>mdi-emoticon-happy-outline</v-icon>
                </v-btn>


              </div>


              <v-btn
                  class="white--text text-capitalize text-body-1"
                  elevation="0"
                  color="#6750A4"
                  font-size="1.2em"
                  @click.stop="sendComment"
              >
                Отправить
              </v-btn>
            </v-row>


          </v-card>

          <v-card
              width="100%"
              height="calc(100% - 115px)"
              elevation="0"
              class="mt-6"
              :class="{['overflow-y-auto pr-2']: comments.length >= 3}"
          >
            <template
                v-for="comment in comments"
            >

              <v-card
                  :key="comment.id"
                  elevation="0"
                  v-if="!comment.isDelete"
              >
                <v-list-item three-line class="pr-0">
                  <v-list-item-avatar
                      left
                      color="#C4C4C4"
                      size="36"
                  >
                    AA
                  </v-list-item-avatar>
                  <v-list-item-content>

                    <div class="text-subtitle-2 text-left mb-1">
                      <span :style="getRandomColor(comment.id)">{{ comment.id }}</span>
                      <span> {{ formatDate(comment.dateCreated) }}</span>
                    </div>

                    <template>
                      <div class="text-justify mb-0 comment-text">
                        {{ comment.commentText }}
                      </div>
                    </template>
                  </v-list-item-content>

                </v-list-item>

                <v-card-actions class="pa-0 ml-15 flex-wrap">
                  <v-btn
                      text
                      :x-small="width <= 768"
                      color="#000000"
                      class="text-capitalize text-body-1 font-weight-bold"
                  >
                    Ответить
                  </v-btn>

                  <v-btn
                      text
                      :x-small="width <= 768"
                      color="rgba(0,0,0,.7)"
                      class="text-capitalize text-body-1 ml-0"
                  >
                    Изменить
                  </v-btn>


                  <v-btn
                      text
                      :x-small="width <= 768"
                      color="rgba(0,0,0,.7)"
                      class="text-capitalize text-body-1 ml-0"
                      @click.stop="openConfirm(comment.id)"
                  >
                    Удалить
                  </v-btn>

                </v-card-actions>

              </v-card>

              <v-card
                  :key="comment.id"
                  elevation="0"
                  v-else
              >
                <v-list-item three-line class="pr-0">
                  <v-list-item-avatar
                      left
                      color="#C4C4C4"
                      size="36"
                  >
                    AA
                  </v-list-item-avatar>
                  <v-list-item-content>

                    <div class="text-subtitle-2 text-left mb-1">
                      <span :style="getRandomColor(comment.id)">{{ comment.id }}</span>
                    </div>

                    <template>
                      <div class="text-justify mb-0 comment-text__deleted">
                        Комментарий удален &nbsp; {{ today }}
                      </div>
                    </template>
                  </v-list-item-content>

                </v-list-item>

              </v-card>


            </template>

          </v-card>

        </v-card>
      </v-col>
    </v-row>

    <ConfirmModal :dialog="isConfirm" @deleteComment=delComment @closeConfirm="isConfirm=false"/>

  </v-container>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import ConfirmModal from "@/components/ConfirmModal";

export default {
  name: "Comments",
  components: {ConfirmModal},
  comments: {
    ConfirmModal,
  },

  data() {
    return {
      commentObj: {
        path: "",
        page_id: "1",
        comment_text: "",
        is_private: true,
        attachment: [],
        user_id: "78",
      },
      colors: ["#0038FF", "#FE0000", "#28A015"],
      today: new Date(),
      width: 0,
      isConfirm: false,
      commentId: 0,
    };
  },

  methods: {
    ...mapActions([
      "fetchComments",
      "SET_COMMENT",
      "DEL_COMMENT",
    ]),

    /**
     * Метод отправки коментария
     */
    async sendComment() {
      if (this.commentObj.comment_text !== "") {
        let regexp = /"(\\.|[^"\\])*"/g;
        this.commentObj.comment_text.match(regexp);
        await this.SET_COMMENT(this.commentObj);

        this.commentObj = {
          path: "",
          page_id: "1",
          comment_text: "",
          is_private: true,
          attachment: [],
          user_id: "78",
        };
      }
    },

    async delComment() {
      await this.DEL_COMMENT(this.commentId);

      const options = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
      };
      this.today = Intl.DateTimeFormat("ru-Ru", options).format(this.today);
      this.isConfirm = false;
    },

    /**
     * Метод форматирования даты
     * @param date
     * @returns {string}
     */
    formatDate(date) {
      let newDate = new Date(date);
      const options = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
        second: "2-digit",
      };
      return new Intl.DateTimeFormat("ru-Ru", options).format(newDate);
    },

    /**
     * Метод установления рандомного цвета для имени пользователя
     * @returns {{color: string}}
     */
    getRandomColor() {
      return {
        color: this.colors[Math.floor(Math.random() * this.colors.length)],
      };
    },

    /**
     * Метод установки рандомного цвета текста для имени пользователя
     */
    handleResize() {
      this.width = window.innerWidth;
    },

    /**
     * Метод открытия модвльногшо окна подтверждения удаления
     * @param id
     */
    openConfirm(id) {
      this.commentId = id;
      this.isConfirm = true
    }

  },

  computed: {
    ...mapGetters({
      comments: "COMMENTS",
    }),
  },

  mounted() {
    this.fetchComments("1");
  },

  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },

};
</script>

<style lang="scss">
.message-block {
  height: 100vh;
}

.comment-text {
  width: 100%;
  min-height: 37px;
  white-space: pre-line;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
  border-radius: 5px;
  padding: 8px 12px;

  &__deleted {
    width: 100%;
    min-height: 37px;
    white-space: pre-line;
    background: #FFFFFF;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 8px 12px;
    color: #454545;
  }
}

.v-btn__content {
  letter-spacing: 0;
}

@media (max-width: 768px) {
  .v-btn__content {
    font-size: 14px;
  }
}
</style>
